/**
 * @data mock
 * @message 
 * @success 
 */
class MockRequest {
  public data: unknown;

  public message: string | null;

  public success: boolean;

  constructor(data: unknown, message: string | null, success: boolean) {
    this.data = data;
    this.message = message;
    this.success = success;
  }
}

export interface AjaxDate {
  body: string;
  type: string;
  url: string;
}

export default MockRequest;
